import { View,SafeAreaView, StyleSheet, TextInput, TouchableOpacity, Dimensions,ImageBackground} from 'react-native'
import React from 'react'
import NavOptions from '../components/NavOptions'
import tw from 'twrnc'
import { useNavigation,useRoute} from '@react-navigation/native'
import {auth} from '../firebase';
import {Icon} from 'react-native-elements'

const Home  = () => {
 const navigation = useNavigation();

 const {
    params: {
      email
    }
  } = useRoute();
 let perms = email.slice(email.indexOf('@') + 1);
 console.log(perms)
 const handleSignOut = () => {
  auth
    .signOut()
    .then(() => {
      navigation.replace("Login")
    })
    .catch(error => alert(error.message))
}

   return (
     <>
       <View style={tw`flex-row bg-white`}>
       <TouchableOpacity style={{alignSelf:'flex-start'}} onPress={handleSignOut}>
       <Icon name="user" type="antdesign" color="black" size={35}/>
       </TouchableOpacity>
       </View>
    <SafeAreaView style={tw.style('flex-1 bg-white')}>
      <View style={{paddingLeft: 10,flex:1}}>
        <NavOptions perms={perms}/>
      </View>
    </SafeAreaView>
    </>
   
  )
}

export default Home

const styles = StyleSheet.create({

});
